// ============================================
// LOWORLD - iCloud Style Design
// Premium Glass Morphism + Clean Layout
// ============================================

// CSS Variables
$font-stack: -apple-system, BlinkMacSystemFont, 'SF Pro Display', 'Helvetica Neue', Roboto, sans-serif;
$tile-radius: 24px;

:host {
  // Default (Light)
  --bg-gradient: linear-gradient(135deg, #0061ff 0%, #60efff 100%);
  --text-primary: #1d1d1f;
  --text-secondary: #333333;
  --tile-radius: 24px;
  --tile-bg: rgba(255, 255, 255, 0.65);
  --tile-bg-hover: rgba(255, 255, 255, 0.8);
  --tile-border: rgba(255, 255, 255, 0.4);
  --tile-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.15);
  --blob-1: #ffffff;
  --blob-2: #00c6fb;
  --blob-3: #005bea;
  --blob-4: #ffffff;
  --toolbar-bg: rgba(255, 255, 255, 0.3);
  --toolbar-border: rgba(255, 255, 255, 0.2);
  --toolbar-text: #1d1d1f;
  --toolbar-btn-hover: rgba(0, 0, 0, 0.06);
  --profile-avatar-bg: #f5f5f7;
  --profile-avatar-shadow: 0 8px 24px rgba(0,0,0,0.06);
  --profile-icon-color: #98989d;
  --service-list-bg: rgba(60, 60, 67, 0.08);
  --service-row-bg: rgba(255, 255, 255, 0.85);
  --service-row-hover: rgba(255, 255, 255, 1);
  --service-row-active: rgba(242, 242, 247, 0.8);
  --service-featured-bg: rgba(255, 245, 245, 0.9);
  --service-featured-hover: rgba(255, 240, 240, 1);
  --price-color: #007aff;
  --chevron-color: #888888;
  --intro-card-bg: rgba(255, 255, 255, 0.6);
  --intro-card-border: rgba(255, 255, 255, 0.4);
  --intro-stat-bg: rgba(255, 255, 255, 0.8);
  --intro-stat-border: rgba(0, 0, 0, 0.06);
  --blob-mix-blend: overlay;
  --blob-opacity: 0.8;
}

:host-context(.dark-theme) {
  // Dark - Premium Deep Blue/Slate
  --bg-gradient: linear-gradient(135deg, #020617 0%, #0f172a 50%, #1e1b4b 100%);
  --text-primary: #f8fafc;
  --text-secondary: #94a3b8;
  --tile-radius: 24px;
  --tile-bg: rgba(30, 41, 59, 0.5);
  --tile-bg-hover: rgba(51, 65, 85, 0.6);
  --tile-border: rgba(255, 255, 255, 0.08);
  --tile-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
  --blob-1: #6366f1;
  --blob-2: #a855f7;
  --blob-3: #3b82f6;
  --blob-4: #ec4899;
  --toolbar-bg: rgba(15, 23, 42, 0.7);
  --toolbar-border: rgba(255, 255, 255, 0.08);
  --toolbar-text: #f8fafc;
  --toolbar-btn-hover: rgba(255, 255, 255, 0.1);
  --profile-avatar-bg: rgba(255, 255, 255, 0.05);
  --profile-avatar-shadow: 0 8px 32px rgba(0,0,0,0.4);
  --profile-icon-color: #94a3b8;
  --service-list-bg: rgba(255, 255, 255, 0.03);
  --service-row-bg: rgba(30, 41, 59, 0.4);
  --service-row-hover: rgba(51, 65, 85, 0.6);
  --service-row-active: rgba(15, 23, 42, 0.8);
  --service-featured-bg: rgba(99, 102, 241, 0.15);
  --service-featured-hover: rgba(99, 102, 241, 0.25);
  --price-color: #818cf8;
  --chevron-color: #64748b;
  --intro-card-bg: rgba(30, 41, 59, 0.5);
  --intro-card-border: rgba(255, 255, 255, 0.08);
  --intro-stat-bg: rgba(15, 23, 42, 0.4);
  --intro-stat-border: rgba(255, 255, 255, 0.05);
  --blob-mix-blend: screen;
  --blob-opacity: 0.3;
}

// ============================================
// Homepage Viewport
// ============================================
.homepage-viewport {
  position: relative;
  min-height: 100vh;
  font-family: $font-stack;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background-color: transparent;
  color: var(--text-primary);
}

// ============================================
// Gradient Background
// ============================================
.homepage-gradient-background {
  position: fixed;
  inset: 0;
  z-index: -1;
  background: var(--bg-gradient);
  overflow: hidden;
}

// ============================================
// Toolbar (Header)
// ============================================
.cloudos-toolbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 52px;
  padding: 0 24px;
  background: var(--toolbar-bg);
  backdrop-filter: saturate(180%) blur(25px);
  -webkit-backdrop-filter: saturate(180%) blur(25px);
  border-bottom: 1px solid var(--toolbar-border);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
}

.icloud-logo {
  .logo-link {
    display: flex;
    align-items: center;
    gap: 8px;
    text-decoration: none;
    color: var(--toolbar-text);
  }

  .logo-icon {
    width: 28px;
    height: 28px;
    color: #3b82f6;
  }

  .logo-text {
    font-size: 19px;
    font-weight: 600;
    letter-spacing: -0.4px;
  }
}

.action-buttons {
  display: flex;
  align-items: center;
  gap: 12px;

  .user-toolbar-info {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 4px 4px 4px 12px;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.1);
    cursor: pointer;
    transition: all 0.2s ease;

    &:hover {
      background: rgba(255, 255, 255, 0.2);
      border-color: rgba(255, 255, 255, 0.3);
    }

    .user-text-info {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      
      .user-name {
        font-size: 12px;
        font-weight: 600;
        color: var(--toolbar-text);
      }
      
      .user-email {
        font-size: 10px;
        color: var(--toolbar-text);
        opacity: 0.7;
      }
    }

    .profile-btn {
      width: 28px;
      height: 28px;
      background: rgba(255, 255, 255, 0.2);
      color: var(--toolbar-text);
      margin: 0;
    }
  }
}

.toolbar-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 50%;
  background: transparent;
  color: var(--toolbar-text);
  cursor: pointer;
  transition: background 0.2s;

  &:hover {
    background: rgba(255, 255, 255, 0.1);
  }

  &.primary {
    background: #3b82f6;
    color: white;
    text-decoration: none;

    &:hover {
      background: #2563eb;
    }
  }
}

// ============================================
// Scrollview Container
// ============================================
.homepage-scrollview {
  padding-top: 52px;
  min-height: 100vh;
}

.homepage-content {
  max-width: 1080px;
  margin: 0 auto;
  padding: 40px 24px;
}

.home-page {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

// ============================================
// Tile Grid (iCloud style - alternating layout)
// ============================================
.tile-grid {
  display: grid;
  grid-template-columns: 1fr 1.6fr;
  grid-auto-rows: auto;
  gap: 24px;
  align-items: stretch;

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    gap: 20px;
  }
}

// ============================================
// Tile Container
// ============================================
.tile-container {
  &.tile-small {
    grid-column: span 1;
  }

  &.tile-large {
    grid-column: span 1;
  }

  &.tile-full {
    grid-column: 1 / -1;
  }

  // 모든 타일 높이 맞춤
  .tile {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  @media (max-width: 767px) {
    &.tile-small,
    &.tile-large,
    &.tile-full {
      grid-column: span 1;
    }
  }
}

// ============================================
// Tile Base - Glass Morphism
// ============================================
.tile {
  position: relative;
  background: var(--tile-bg);
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
  border-radius: var(--tile-radius);
  box-shadow: var(--tile-shadow);
  border: 1px solid var(--tile-border);
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.16, 1, 0.3, 1);
  height: 100%;
  display: flex;
  flex-direction: column;

  &:hover {
    transform: translateY(-5px) scale(1.01);
    background: var(--tile-bg-hover);
    box-shadow: 0 12px 40px 0 rgba(0, 0, 0, 0.4);
    border: 1px solid rgba(255, 255, 255, 0.2);
    z-index: 10;
  }

  .tile-border {
    display: none;
  }
}

// ============================================
// Tile Banner
// ============================================
.tile-banner {
  padding: 20px 20px 16px;

  .banner-link {
    display: flex;
    align-items: center;
    gap: 14px;
    text-decoration: none;
    color: inherit;
  }

  &.compact {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px;
  }
}

.banner-text {
  .tile-heading {
    font-size: 17px;
    font-weight: 600;
    color: var(--text-primary);
    line-height: 1.3;
  }

  .tile-description {
    font-size: 13px;
    color: var(--text-secondary);
    margin-top: 2px;
    font-weight: 500;
  }
}

// ============================================
// App Icon Wrapper
// ============================================
.app-icon-wrapper {
  flex-shrink: 0;
}

.app-icon {
  width: 56px;
  height: 56px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 
    0 4px 8px rgba(0, 0, 0, 0.12), 
    0 8px 16px rgba(0, 0, 0, 0.08),
    inset 0 1px 1px rgba(255, 255, 255, 0.3);
  position: relative;
  overflow: hidden;

  // Glossy effect
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    background: linear-gradient(180deg, rgba(255,255,255,0.2) 0%, rgba(255,255,255,0) 40%);
    pointer-events: none;
  }

  svg {
    width: 28px;
    height: 28px;
    color: white;
    filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.15));
  }

  // iOS System Colors with gradients
  &.rocket { background: linear-gradient(135deg, #d488ff 0%, #af52de 100%); }
  &.code { background: linear-gradient(135deg, #64d2ff 0%, #007aff 100%); }
  &.stats { background: linear-gradient(135deg, #34c759 0%, #30b0c7 100%); }
  &.purple { background: linear-gradient(135deg, #d488ff 0%, #af52de 100%); }
  &.blue { background: linear-gradient(135deg, #64d2ff 0%, #007aff 100%); }
  &.orange { background: linear-gradient(135deg, #ffd60a 0%, #ff9f0a 100%); }
  &.cyan { background: linear-gradient(135deg, #70d7ff 0%, #5ac8fa 100%); }
  &.green { background: linear-gradient(135deg, #34c759 0%, #30b0c7 100%); }
  &.red { background: linear-gradient(135deg, #ff6b6b 0%, #ff3b30 100%); }
  &.teal { background: linear-gradient(135deg, #64d2ff 0%, #30b0c7 100%); }
  &.pink { background: linear-gradient(135deg, #ff6b9d 0%, #ff2d55 100%); }
  &.indigo { background: linear-gradient(135deg, #7c7ce0 0%, #5856d6 100%); }
  &.gray { background: linear-gradient(135deg, #a8a8a8 0%, #8e8e93 100%); }
  
  // Software Development Icon - Premium gradient
  &.software-dev {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
    box-shadow: 
      0 4px 15px rgba(102, 126, 234, 0.4),
      0 8px 25px rgba(118, 75, 162, 0.3),
      inset 0 1px 1px rgba(255, 255, 255, 0.4);
  }
}

// ============================================
// Profile Tile (iCloud style - Centered)
// ============================================
.profile-tile {
  padding: 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  .profile-avatar {
    position: relative;
    width: 104px;
    height: 104px;
    margin-bottom: 20px;
    background: var(--profile-avatar-bg);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--profile-avatar-shadow);

    .cloud-bg {
      display: none;
    }

    .avatar-icon {
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
        width: 52px;
        height: 52px;
        color: var(--profile-icon-color);
      }
    }
  }

  .user-info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .user-name {
    font-size: 24px;
    font-weight: 700;
    color: var(--text-primary);
    line-height: 1.2;
    letter-spacing: -0.5px;
    margin-bottom: 6px;
  }

  .user-email {
    font-size: 15px;
    color: var(--text-secondary);
    font-weight: 500;
  }

  .plan-badge {
    margin-top: 20px;
    padding: 6px 14px;
    background: rgba(0, 122, 255, 0.1);
    color: #007aff;
    font-size: 13px;
    font-weight: 600;
    border-radius: 100px;
    letter-spacing: 0;
  }
}

// ============================================
// Services Tile (iCloud style)
// ============================================
.services-tile {
  .tile-content {
    padding: 0 20px 24px;
    flex: 1;
    display: flex;
    flex-direction: column;
    
    // Tile Slider Override
    &.tile-slider {
      padding: 0;
      overflow: hidden;
      flex-direction: row;
      width: 200%;
      transition: transform 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
      
      &.show-detail {
        transform: translateX(-50%);
      }
      
      .tile-pane {
        width: 50%;
        flex-shrink: 0;
        
        &.service-list-pane {
          padding: 0 20px 24px;
          overflow-y: auto;
          max-height: 100%;
        }
        
        &.detail-pane {
          padding: 16px 20px 24px;
          display: flex;
          flex-direction: column;
          gap: 12px;
          background: var(--tile-bg);
        }
      }
    }
  }

  .service-list {
    display: flex;
    flex-direction: column;
    gap: 1px;
    background: var(--service-list-bg);
    border-radius: 14px;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0,0,0,0.2);
  }

  .service-row {
    display: flex;
    align-items: center;
    gap: 14px;
    padding: 14px 16px;
    background: var(--service-row-bg);
    text-decoration: none;
    color: inherit;
    position: relative;
    transition: background 0.2s ease;
    // backdrop-filter: blur(5px);

    &:hover {
      background: var(--service-row-hover);
    }

    &:active {
      background: var(--service-row-active);
    }

    &.featured {
      background: var(--service-featured-bg);

      &:hover {
        background: var(--service-featured-hover);
      }
    }

    .featured-badge {
      position: absolute;
      top: 6px;
      right: 10px;
      padding: 3px 8px;
      background: #ff3b30;
      color: white;
      font-size: 10px;
      font-weight: 600;
      border-radius: 4px;
      animation: badge-pulse 2s infinite;
    }

    .service-icon {
      width: 42px;
      height: 42px;
      border-radius: 10px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      box-shadow: 0 2px 6px rgba(0,0,0,0.12), inset 0 1px 0 rgba(255,255,255,0.2);

      svg {
        width: 24px;
        height: 24px;
        filter: drop-shadow(0 1px 2px rgba(0,0,0,0.1));
      }

      // Colors
      &.purple { background: linear-gradient(135deg, #d488ff 0%, #af52de 100%); }
      &.blue { background: linear-gradient(135deg, #64d2ff 0%, #007aff 100%); }
      &.orange { background: linear-gradient(135deg, #ffd60a 0%, #ff9f0a 100%); }
      &.cyan { background: linear-gradient(135deg, #70d7ff 0%, #5ac8fa 100%); }
      &.green { background: linear-gradient(135deg, #34c759 0%, #30b0c7 100%); }
      &.red { background: linear-gradient(135deg, #ff6b6b 0%, #ff3b30 100%); }
      &.pink { background: linear-gradient(135deg, #ff6b9d 0%, #ff2d55 100%); }
    }

    .service-info {
      flex: 1;
      min-width: 0;

      .service-name {
        font-size: 15px;
        font-weight: 500;
        color: var(--text-primary);
        line-height: 1.3;
      }

      .service-meta {
        display: flex;
        align-items: center;
        gap: 8px;
        margin-top: 3px;

        .meta-text {
          font-size: 13px;
          color: var(--text-secondary);
          font-weight: 500;
        }

        .meta-price {
          font-size: 13px;
          font-weight: 600;
          color: var(--price-color);

          &.live {
            color: #30d158;
          }

          &.discount {
            color: #ff453a;

            del {
              color: #636366;
              font-weight: 400;
              margin-right: 4px;
            }
          }
        }
      }
    }

    .chevron {
      width: 18px;
      height: 18px;
      flex-shrink: 0;
      color: var(--chevron-color);

      svg {
        width: 100%;
        height: 100%;
      }
    }

    &:hover .chevron {
      color: var(--text-secondary);
    }
  }
}

@keyframes badge-pulse {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.85; transform: scale(1.02); }
}

// ============================================
// Tech Tile (iCloud style)
// ============================================
.tech-tile {
  .tech-content {
    padding: 0 16px 20px;
    flex: 1;
  }

  .tech-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }

  .tech-tag {
    padding: 8px 14px;
    background: var(--profile-avatar-bg);
    border-radius: 8px;
    font-size: 13px;
    font-weight: 500;
    color: var(--text-primary);
    transition: all 0.15s ease;
    cursor: pointer;

    &:hover {
      background: #007aff;
      color: white;
    }
  }
}

// ============================================
// App Library Tile (iCloud Grid)
// ============================================
.app-library {
  padding: 16px;

  .app-icon-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px 8px;
  }

  .app-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    text-decoration: none;
    padding: 8px 4px;
    border-radius: 12px;
    transition: transform 0.15s ease;

    &:hover {
      transform: scale(1.02);
    }

    &:active {
      transform: scale(0.98);
    }

    .app-icon {
      width: 48px;
      height: 48px;
      border-radius: 12px;
      box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);

      svg {
        width: 26px;
        height: 26px;
      }
    }

    .app-name {
      font-size: 12px;
      font-weight: 500;
      color: var(--text-primary);
      text-align: center;
      line-height: 1.3;
      max-width: 64px;
    }
  }
}

// ============================================
// App Library Wide Tile (Full Width - iOS Style)
// ============================================
.app-library-wide {
  padding: 0;
  display: flex;
  flex-direction: column;
  height: 100%;

  // App Navigator Styles
  .app-navigator-header {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 16px 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.18);
    background: rgba(255, 255, 255, 0.1);
    
    .title {
      font-size: 14px;
      font-weight: 600;
      color: rgba(0, 0, 0, 0.7);
    }

    .badge {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      min-width: 24px;
      height: 22px;
      padding: 0 8px;
      background: #667eea;
      border-radius: 11px;
      font-size: 12px;
      font-weight: 700;
      color: white;
      line-height: 1;
    }
  }

  .app-navigator-container {
    position: relative;
    min-height: 280px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 24px 20px 16px;
  }

  .app-icon-grid-paginated {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, auto);
    gap: 24px 16px;
    justify-items: center;
    
    .app-link {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 8px;
      text-decoration: none;
      color: var(--text-primary);
      transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
      cursor: pointer;
      
      .app-icon {
        width: 72px;
        height: 72px;
        border-radius: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
        
        svg {
          width: 32px;
          height: 32px;
          color: white;
        }

        // iOS 스타일 선명한 색상 그라데이션
        &.blue { background: linear-gradient(135deg, #5ac8fa 0%, #007aff 100%); }
        &.green { background: linear-gradient(135deg, #4cd964 0%, #34c759 100%); }
        &.purple { background: linear-gradient(135deg, #bf5af2 0%, #af52de 100%); }
        &.orange { background: linear-gradient(135deg, #ffcc00 0%, #ff9500 100%); }
        &.pink { background: linear-gradient(135deg, #ff6b9d 0%, #ff2d55 100%); }
        &.teal { background: linear-gradient(135deg, #64d2ff 0%, #5ac8fa 100%); }
        &.red { background: linear-gradient(135deg, #ff6b6b 0%, #ff3b30 100%); }
        &.gray { background: linear-gradient(135deg, #98989d 0%, #636366 100%); }
      }
      
      .app-name {
        font-size: 12px;
        font-weight: 500;
        text-align: center;
        letter-spacing: -0.2px;
        max-width: 80px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: rgba(0, 0, 0, 0.85);
      }
      
      &:hover {
        .app-icon {
          transform: scale(1.08);
          box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
        }
      }
      
      &:active {
        .app-icon {
          transform: scale(0.95);
        }
      }
      
      &.active {
        .app-icon {
          box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.5), 0 6px 20px rgba(0, 0, 0, 0.15);
          transform: scale(1.05);
        }
        
        .app-name {
          font-weight: 600;
          color: #667eea;
        }
      }
    }
  }

  .pagination-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding-top: 20px;
    
    .dot {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: rgba(0, 0, 0, 0.2);
      cursor: pointer;
      transition: all 0.3s ease;
      
      &.active {
        width: 24px;
        background: #667eea;
        border-radius: 4px;
      }
      
      &:hover:not(.active) {
        background: rgba(0, 0, 0, 0.4);
      }
    }
  }
}

// Dark mode adjustments
:host-context(.dark-theme) {
  .app-library-wide {
    .app-navigator-header {
      border-bottom-color: rgba(255, 255, 255, 0.1);
      background: rgba(0, 0, 0, 0.2);
      
      .title { color: rgba(255, 255, 255, 0.8); }
    }
    
    .app-icon-grid-paginated .app-link {
      .app-name {
        color: rgba(255, 255, 255, 0.9);
      }
      
      &.active .app-name {
        color: #8ba1ff;
      }
    }
    
    .pagination-dots .dot {
      background: rgba(255, 255, 255, 0.3);
      
      &.active {
        background: #8ba1ff;
      }
      
      &:hover:not(.active) {
        background: rgba(255, 255, 255, 0.4);
      }
    }
  }
}

// ============================================
// Stats Tile (iCloud style)
// ============================================
.stats-tile {
  .tile-content {
    padding: 0 16px 20px;
  }

  .stats-showcase {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 8px;
  }

  .stat-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6px;
    text-align: center;
    padding: 8px 4px;

    .stat-circle {
      width: 56px;
      height: 56px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.12);

      .stat-number {
        font-size: 16px;
        font-weight: 700;
        color: white;
        letter-spacing: -0.3px;
      }

      &.blue { background: #007aff; }
      &.purple { background: #af52de; }
      &.green { background: #34c759; }
      &.orange { background: #ff9500; }
    }

    .stat-label {
      font-size: 11px;
      font-weight: 500;
      color: var(--text-secondary);
    }
  }
}

// ============================================
// Customize Section
// ============================================
.customize-section {
  display: flex;
  justify-content: center;
  padding: 16px 0;
}

.customize-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 28px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  color: white;
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: translateY(-1px);
  }

  svg {
    opacity: 0.9;
  }
}

// ============================================
// Footer
// ============================================
.legal-footer {
  padding: 24px 16px 32px;
  text-align: center;

  .footer-content {
    max-width: 600px;
    margin: 0 auto;
  }

  .footer-links {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 12px;

    .footer-link {
      color: rgba(255, 255, 255, 0.8);
      font-size: 12px;
      text-decoration: none;
      transition: color 0.2s;

      &:hover {
        color: white;
        text-decoration: underline;
      }
    }

    .separator {
      color: rgba(255, 255, 255, 0.4);
      font-size: 12px;
    }
  }

  .copyright {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.6);
  }
}

// ============================================
// Fade In Animation
// ============================================
.fade-in {
  opacity: 0;
  transform: translateY(16px);
  transition: opacity 0.5s ease, transform 0.5s ease;

  &.visible {
    opacity: 1;
    transform: translateY(0);
  }
}

// ============================================
// Responsive Adjustments
// ============================================
@media (max-width: 767px) {
  .homepage-content {
    padding: 16px 16px 80px; // Added bottom padding for scroll
  }

  .tile-grid {
    grid-template-columns: 1fr;
    gap: 16px; // Increased gap slightly for better separation
  }

  .tile-container {
    &.tile-small,
    &.tile-large,
    &.tile-full {
      grid-column: span 1;
    }

    .tile {
      height: auto;
      min-height: auto; // Ensure no fixed height forces empty space
    }
  }

  .profile-tile {
    padding: 24px 20px;
    align-items: center;
    text-align: center;

    .profile-avatar {
      width: 80px;
      height: 80px;
      margin-bottom: 16px;

      .avatar-icon svg {
        width: 40px;
        height: 40px;
      }
    }

    .user-name {
      font-size: 20px;
      margin-bottom: 4px;
    }

    .user-email {
      font-size: 14px;
    }

    .plan-badge {
      margin-top: 16px;
    }
  }

  .stats-tile .stats-showcase {
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;

    .stat-circle {
      width: 48px;
      height: 48px;

      .stat-number {
        font-size: 14px;
      }
    }
  }

  .app-library .app-icon-grid {
    grid-template-columns: repeat(4, 1fr); // 4 cols for icons on mobile
    gap: 16px 8px;
  }

  .app-library-wide {
    padding: 16px 0;

    .app-icon-grid-paginated {
      gap: 16px;
      padding: 0 16px;
    }

    .app-link {
      min-width: 60px;
      padding: 8px 4px;

      .app-icon {
        width: 52px;
        height: 52px;
        border-radius: 12px;

        svg {
          width: 26px;
          height: 26px;
        }
      }

      .app-name {
        font-size: 11px;
      }
    }
  }

  .cloudos-toolbar {
    padding: 0 16px;
    
    .logo-text {
      font-size: 17px;
    }
  }

  // Curriculum responsive
  .curriculum-section {
    padding: 20px;
    margin: 24px 0;

    .curriculum-header {
      flex-direction: column;
      align-items: flex-start;
      gap: 16px;
    }

    .curriculum-intro {
      gap: 16px;
    }

    .intro-stats {
      grid-template-columns: repeat(2, 1fr);
    }

    .week-items {
      flex-direction: column;
      gap: 12px;
      align-items: stretch; // Full width items
    }

    .week-item {
      width: 100%;
      box-sizing: border-box;
    }

    .week-arrow {
      transform: rotate(90deg);
      align-self: center;
      margin: 4px 0;
    }

    .month-grid {
      grid-template-columns: repeat(2, 1fr); // 2 cols for months
    }

    .launch-grid {
      grid-template-columns: 1fr;
    }

    .curriculum-cta {
      flex-direction: column;
      text-align: center;
      padding: 24px;

      .cta-content {
        margin-bottom: 20px;
      }

      .cta-price {
        justify-content: center;
        margin-bottom: 20px;
      }

      .cta-button {
        width: 100%;
        justify-content: center;
      }
    }
  }

  // New sections responsive
  .hero-story {
    padding: 24px;
    margin-bottom: 24px;

    .story-quote {
      blockquote {
        font-size: 18px;
        line-height: 1.4;
      }
      
      .quote-icon {
        font-size: 32px;
      }
    }
  }

  .why-now-grid,
  .values-grid,
  .target-grid {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .operation-grid {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .provide-grid {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .vision-section {
    padding: 24px 20px;

    .vision-main {
      font-size: 16px;
    }

    .vision-sub {
      font-size: 14px;
    }
  }
  
  // Service Tile adjustments
  .services-tile {
    .tile-content {
      padding: 0 16px 20px;
    }
    
    .service-row {
      padding: 12px;
      gap: 12px;
      
      .service-icon {
        width: 36px;
        height: 36px;
        
        svg {
          width: 20px;
          height: 20px;
        }
      }
      
      .service-name {
        font-size: 14px;
      }
      
      .meta-text, .meta-price {
        font-size: 12px;
      }
    }
  }
}

// ============================================
// Curriculum Section Styles
// ============================================
.curriculum-section {
  margin: 32px 0;
  background: var(--tile-bg);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border-radius: var(--tile-radius);
  border: 1px solid var(--tile-border);
  box-shadow: var(--tile-shadow);
  padding: 32px;
  overflow: hidden;
}

.curriculum-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 28px;
  flex-wrap: wrap;
}

.curriculum-icon {
  width: 56px;
  height: 56px;
  border-radius: 14px;
  background: linear-gradient(145deg, #5ac8fa 0%, #007aff 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 12px rgba(0, 122, 255, 0.3);

  svg {
    width: 32px;
    height: 32px;
    color: white;
  }
}

.curriculum-title-wrapper {
  flex: 1;
}

.curriculum-title {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
  letter-spacing: -0.5px;
}

.curriculum-subtitle {
  font-size: 15px;
  color: var(--text-secondary);
  margin: 4px 0 0 0;
  font-weight: 500;
}

.curriculum-badge {
  background: linear-gradient(135deg, #ff6b6b 0%, #ff3b30 100%);
  color: white;
  font-size: 12px;
  font-weight: 600;
  padding: 6px 14px;
  border-radius: 20px;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

// Intro Section
.curriculum-intro {
  display: flex;
  gap: 24px;
  margin-bottom: 32px;
  flex-wrap: wrap;
}

.intro-card {
  flex: 1;
  min-width: 280px;
  background: var(--intro-card-bg);
  backdrop-filter: blur(20px);
  border-radius: var(--tile-radius);
  padding: 24px;
  border: 1px solid var(--intro-card-border);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);

  .intro-icon {
    font-size: 40px;
    margin-bottom: 12px;
  }

  h3 {
    font-size: 20px;
    font-weight: 700;
    color: var(--text-primary);
    margin: 0 0 8px 0;
  }

  p {
    font-size: 15px;
    color: var(--text-secondary);
    margin: 0;
    line-height: 1.5;
    font-weight: 500;
  }
}

.intro-stats {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 12px;
  flex: 1;
  min-width: 280px;
}

.intro-stat {
  background: var(--intro-stat-bg);
  border-radius: var(--tile-radius);
  padding: 16px;
  text-align: center;
  border: 1px solid var(--intro-stat-border);

  .stat-value {
    display: block;
    font-size: 24px;
    font-weight: 700;
    color: var(--text-primary);
  }
  
  .stat-desc {
    font-size: 12px;
    color: var(--text-secondary);
    margin-top: 4px;
  }
}


// Phase Section
.curriculum-phase {
  margin-bottom: 28px;
  background: var(--tile-bg);
  border-radius: var(--tile-radius);
  padding: 24px;
  border: 1px solid var(--tile-border);
}

.phase-header {
  margin-bottom: 20px;
}

.phase-badge {
  display: inline-block;
  font-size: 11px;
  font-weight: 700;
  padding: 4px 12px;
  border-radius: 12px;
  margin-bottom: 8px;

  &.development {
    background: linear-gradient(135deg, #5856d6 0%, #af52de 100%);
    color: white;
  }

  &.launch {
    background: linear-gradient(135deg, #34c759 0%, #30d158 100%);
    color: white;
  }
}

.phase-title {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 6px 0;
}

.phase-desc {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

// Weekly Structure
.weekly-structure {
  background: rgba(88, 86, 214, 0.06);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 24px;
  border: 1px solid rgba(88, 86, 214, 0.1);
}

.structure-title {
  font-size: 15px;
  font-weight: 600;
  color: #818cf8;
  margin: 0 0 16px 0;
}

.week-items {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  flex-wrap: wrap;
}

.week-item {
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--tile-bg);
  padding: 12px 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.week-icon {
  font-size: 24px;
}

.week-content {
  display: flex;
  flex-direction: column;
}

.week-label {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

.week-desc {
  font-size: 12px;
  color: var(--text-secondary);
}

.week-arrow {
  font-size: 20px;
  color: #818cf8;
  font-weight: bold;
}

// Month Grid
.month-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 12px;
}

.month-card {
  background: var(--tile-bg);
  border-radius: 12px;
  padding: 16px;
  border: 1px solid var(--tile-border);
  transition: all 0.2s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: var(--tile-shadow, 0 4px 16px rgba(0, 0, 0, 0.1));
  }
}

.month-number {
  display: inline-block;
  font-size: 11px;
  font-weight: 700;
  color: white;
  background: linear-gradient(135deg, #5856d6 0%, #af52de 100%);
  padding: 3px 8px;
  border-radius: 6px;
  margin-bottom: 10px;
}

.month-info {
  h4 {
    font-size: 14px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 4px 0;
  }

  p {
    font-size: 12px;
    color: var(--text-secondary);
    margin: 0 0 8px 0;
    line-height: 1.4;
  }
}

.month-output {
  display: inline-block;
  font-size: 11px;
  color: #5ac8fa;
  background: rgba(90, 200, 250, 0.1);
  padding: 4px 8px;
  border-radius: 6px;
}

// Launch Phase
.launch-phase {
  background: rgba(52, 199, 89, 0.04);
  border: 1px solid rgba(52, 199, 89, 0.15);
}

.launch-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

.launch-card {
  background: var(--tile-bg);
  border-radius: var(--tile-radius);
  padding: 20px;
  border: 1px solid rgba(52, 199, 89, 0.15);

  .launch-icon {
    font-size: 32px;
    margin-bottom: 12px;
  }

  h4 {
    font-size: 16px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 12px 0;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      font-size: 13px;
      color: var(--text-secondary);
      padding: 6px 0;
      border-bottom: 1px solid var(--tile-border, rgba(0, 0, 0, 0.04));
      display: flex;
      align-items: center;
      gap: 8px;

      &::before {
        content: '✓';
        color: #34c759;
        font-weight: bold;
      }

      &:last-child {
        border-bottom: none;
      }
    }
  }
}

// CTA Section
.curriculum-cta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  border-radius: 16px;
  padding: 28px 32px;
  margin-top: 8px;
  border: 1px solid var(--tile-border);
}

.cta-content {
  h3 {
    font-size: 20px;
    font-weight: 700;
    color: white;
    margin: 0 0 6px 0;
  }

  p {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.8);
    margin: 0 0 12px 0;
  }
}

.cta-price {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.original-price {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.4);
  text-decoration: line-through;
}

.discount-price {
  font-size: 24px;
  font-weight: 700;
  color: #5ac8fa;
}

.discount-badge {
  background: #ff3b30;
  color: white;
  font-size: 11px;
  font-weight: 700;
  padding: 4px 10px;
  border-radius: 12px;
}

.cta-button {
  display: flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, #007aff 0%, #5856d6 100%);
  color: white;
  font-size: 16px;
  font-weight: 600;
  padding: 14px 28px;
  border-radius: 12px;
  text-decoration: none;
  transition: all 0.2s ease;
  white-space: nowrap;

  svg {
    width: 20px;
    height: 20px;
  }

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 122, 255, 0.4);
  }
}

// Medium Screen Responsive
@media (max-width: 1024px) {
  .month-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .intro-stats {
    grid-template-columns: repeat(2, 1fr);
  }

  .why-now-grid {
    grid-template-columns: 1fr;
  }

  .values-grid {
    grid-template-columns: 1fr;
  }

  .operation-grid {
    grid-template-columns: 1fr;
  }

  .provide-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .target-grid {
    grid-template-columns: 1fr;
  }
}

// ============================================
// Hero Story Section
// ============================================
.hero-story {
  margin-bottom: 32px;
  background: linear-gradient(135deg, rgba(88, 86, 214, 0.1) 0%, rgba(175, 82, 222, 0.1) 100%);
  border-radius: 20px;
  padding: 32px;
  border: 1px solid rgba(88, 86, 214, 0.2);
}

.story-quote {
  text-align: center;
  margin-bottom: 24px;

  .quote-icon {
    font-size: 48px;
    color: #818cf8;
    line-height: 1;
    font-family: Georgia, serif;
  }

  blockquote {
    font-size: 20px;
    font-weight: 600;
    color: var(--text-primary);
    line-height: 1.5;
    margin: 0 0 16px 0;
    font-style: italic;
  }

  .quote-context {
    font-size: 14px;
    color: var(--text-secondary);
    margin: 0;
    line-height: 1.6;
  }
}

.story-message {
  text-align: center;
  padding-top: 20px;
  border-top: 1px solid rgba(88, 86, 214, 0.2);

  p {
    font-size: 16px;
    color: var(--text-primary);
    margin: 0;
    line-height: 1.7;

    strong {
      color: #818cf8;
    }
  }
}

// ============================================
// Why Now Section
// ============================================
.why-now-section,
.core-values-section,
.operation-section,
.provide-section,
.target-section {
  margin-bottom: 28px;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 20px;

  .section-icon {
    font-size: 24px;
  }

  h3 {
    font-size: 20px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0;

    .highlight {
      color: #007aff;
    }
  }
}

.why-now-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

.why-card {
  background: var(--tile-bg);
  border-radius: var(--tile-radius);
  padding: 24px;
  border: 1px solid var(--tile-border);
  transition: all 0.2s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
  }

  .why-icon {
    font-size: 36px;
    margin-bottom: 12px;
  }

  h4 {
    font-size: 16px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 8px 0;
  }

  p {
    font-size: 14px;
    color: var(--text-secondary);
    margin: 0;
    line-height: 1.6;

    strong {
      color: var(--text-primary);
    }
  }
}

// ============================================
// Core Values Section
// ============================================
.values-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

.value-card {
  background: var(--tile-bg);
  border-radius: var(--tile-radius);
  padding: 24px;
  border: 1px solid var(--tile-border);
  position: relative;
  transition: all 0.2s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
  }

  &.featured {
    background: linear-gradient(135deg, rgba(255, 59, 48, 0.06) 0%, rgba(255, 149, 0, 0.06) 100%);
    border-color: rgba(255, 59, 48, 0.15);
  }

  .value-badge {
    position: absolute;
    top: 12px;
    right: 12px;
    font-size: 10px;
    font-weight: 700;
    color: white;
    background: linear-gradient(135deg, #ff3b30 0%, #ff9500 100%);
    padding: 4px 10px;
    border-radius: 10px;
  }

  .value-icon {
    font-size: 36px;
    margin-bottom: 12px;
  }

  h4 {
    font-size: 16px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 8px 0;
  }

  p {
    font-size: 14px;
    color: var(--text-secondary);
    margin: 0;
    line-height: 1.6;

    strong {
      color: var(--text-primary);
    }
  }
}

// ============================================
// Operation Section
// ============================================
.operation-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.operation-card {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  background: var(--tile-bg);
  border-radius: var(--tile-radius);
  padding: 20px;
  border: 1px solid var(--tile-border);
  transition: all 0.2s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
  }

  .op-icon {
    width: 48px;
    height: 48px;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    flex-shrink: 0;

    &.green { background: rgba(52, 199, 89, 0.12); }
    &.blue { background: rgba(0, 122, 255, 0.12); }
    &.purple { background: rgba(88, 86, 214, 0.12); }
    &.orange { background: rgba(255, 149, 0, 0.12); }
  }

  .op-content {
    flex: 1;

    h4 {
      font-size: 15px;
      font-weight: 600;
      color: var(--text-primary);
      margin: 0 0 6px 0;
    }

    p {
      font-size: 13px;
      color: var(--text-secondary);
      margin: 0 0 8px 0;
      line-height: 1.5;

      strong {
        color: var(--text-primary);
      }
    }

    .op-tag {
      display: inline-block;
      font-size: 11px;
      font-weight: 600;
      color: #007aff;
      background: rgba(0, 122, 255, 0.08);
      padding: 4px 10px;
      border-radius: 8px;
    }
  }
}

// ============================================
// Provide Section
// ============================================
.provide-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
}

.provide-item {
  display: flex;
  align-items: center;
  gap: 12px;
  background: var(--tile-bg);
  border-radius: var(--tile-radius);
  padding: 16px;
  border: 1px solid var(--tile-border);
  transition: all 0.2s ease;

  &:hover {
    background: rgba(0, 122, 255, 0.04);
    border-color: rgba(0, 122, 255, 0.15);
  }

  .provide-icon {
    font-size: 24px;
    flex-shrink: 0;
  }

  span:last-child {
    font-size: 14px;
    color: var(--text-primary);
    font-weight: 500;
  }
}

// ============================================
// Target Section
// ============================================
.target-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

.target-card {
  background: linear-gradient(135deg, rgba(0, 122, 255, 0.1) 0%, rgba(90, 200, 250, 0.1) 100%);
  border-radius: var(--tile-radius);
  padding: 24px;
  border: 1px solid rgba(0, 122, 255, 0.2);
  text-align: center;
  transition: all 0.2s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(0, 122, 255, 0.2);
  }

  .target-emoji {
    font-size: 48px;
    margin-bottom: 12px;
  }

  h4 {
    font-size: 16px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 8px 0;
  }

  p {
    font-size: 14px;
    color: var(--text-secondary);
    margin: 0;
    line-height: 1.5;

    strong {
      color: #007aff;
    }
  }
}

// ============================================
// Vision Section
// ============================================
.vision-section {
  margin-bottom: 32px;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  border-radius: 20px;
  padding: 40px;
  text-align: center;
  border: 1px solid var(--tile-border);
}

.vision-content {
  .vision-icon {
    font-size: 48px;
    margin-bottom: 16px;
  }

  h3 {
    font-size: 22px;
    font-weight: 700;
    color: white;
    margin: 0 0 20px 0;
  }

  .vision-main {
    font-size: 18px;
    color: rgba(255, 255, 255, 0.9);
    line-height: 1.7;
    margin: 0 0 16px 0;

    strong {
      color: #5ac8fa;
    }
  }

  .vision-sub {
    font-size: 15px;
    color: rgba(255, 255, 255, 0.8);
    line-height: 1.7;
    margin: 0;

    strong {
      color: #ff9500;
    }
  }
}

// ============================================
// Curriculum Divider
// ============================================
.curriculum-divider {
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--tile-border), transparent);
  margin: 32px 0;
}

// ============================================
// Responsive Overrides (Must be at the end)
// ============================================
@media (max-width: 1024px) {
  .why-now-grid,
  .values-grid,
  .target-grid,
  .operation-grid,
  .provide-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .intro-stats {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .month-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 767px) {
  .why-now-grid,
  .values-grid,
  .target-grid,
  .operation-grid,
  .provide-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .intro-stats {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .month-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .app-library .app-icon-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

// ============================================
// Detail Pane Styles (for tile slider)
// ============================================
.detail-pane {
  .detail-header {
    display: flex;
    align-items: center;
  }
  
  .back-btn {
    display: flex;
    align-items: center;
    gap: 4px;
    background: none;
    border: none;
    font-size: 14px;
    font-weight: 600;
    color: #007aff;
    cursor: pointer;
    padding: 6px 10px;
    border-radius: 8px;
    transition: background 0.2s;
    
    &:hover {
      background: rgba(0, 122, 255, 0.1);
    }
    
    svg {
      color: #007aff;
    }
  }
  
  .detail-content {
    text-align: center;
    padding: 8px 0;
  }
  
  .detail-icon {
    width: 56px;
    height: 56px;
    margin: 0 auto 12px;
    border-radius: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    
    svg, mat-icon {
      width: 28px;
      height: 28px;
      font-size: 28px;
      color: white;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    
    // Dynamic colors based on service
    &.purple {
      background: linear-gradient(135deg, #d488ff 0%, #af52de 100%);
      box-shadow: 0 4px 12px rgba(175, 82, 222, 0.3);
    }
    &.blue {
      background: linear-gradient(135deg, #64d2ff 0%, #007aff 100%);
      box-shadow: 0 4px 12px rgba(0, 122, 255, 0.3);
    }
    &.orange {
      background: linear-gradient(135deg, #ffd60a 0%, #ff9f0a 100%);
      box-shadow: 0 4px 12px rgba(255, 159, 10, 0.3);
    }
    &.cyan {
      background: linear-gradient(135deg, #70d7ff 0%, #5ac8fa 100%);
      box-shadow: 0 4px 12px rgba(90, 200, 250, 0.3);
    }
    &.pink {
      background: linear-gradient(135deg, #ff6b9d 0%, #ff2d55 100%);
      box-shadow: 0 4px 12px rgba(255, 45, 85, 0.3);
    }
  }
  
  .detail-title {
    font-size: 18px;
    font-weight: 700;
    color: var(--text-primary);
    margin: 0 0 4px 0;
  }
  
  .detail-subtitle {
    font-size: 13px;
    color: var(--text-secondary);
    margin: 0 0 12px 0;
  }
  
  .detail-desc {
    font-size: 13px;
    color: var(--text-secondary);
    line-height: 1.6;
    margin-bottom: 16px;
    text-align: left;
  }
  
  .detail-features {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 16px;
    
    .feature-item {
      font-size: 13px;
      color: var(--text-primary);
      text-align: left;
      padding: 8px 12px;
      background: rgba(52, 199, 89, 0.08);
      border-radius: 8px;
    }
  }
  
  .apply-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
    width: 100%;
    padding: 14px;
    background: linear-gradient(135deg, #007aff 0%, #5856d6 100%);
    color: white;
    font-size: 15px;
    font-weight: 600;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    text-decoration: none;
    transition: all 0.2s;
    
    &:hover {
      transform: translateY(-1px);
      box-shadow: 0 4px 16px rgba(0, 122, 255, 0.4);
    }
  }
}

// ============================================
// Tablet Responsive (768px - 1024px)
// ============================================
@media (min-width: 768px) and (max-width: 1024px) {
  .app-library-wide .app-icon-grid-paginated {
    grid-template-columns: repeat(3, 1fr);
    gap: 18px 14px;
  }
}

// ===================================
// Education Module Additional Styles
// ===================================

// 학교 섹션
.schools-section {
  padding: 40px 0;
  
  .section-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
    
    h2 {
      font-size: 28px;
      font-weight: 700;
      color: var(--text-primary, #1d1d1f);
      margin: 0;
    }
    
    .view-all-btn {
      display: flex;
      align-items: center;
      gap: 6px;
      padding: 10px 20px;
      background: var(--accent-color, #007AFF);
      color: white;
      border: none;
      border-radius: 20px;
      font-size: 14px;
      font-weight: 600;
      text-decoration: none;
      cursor: pointer;
      transition: all 0.3s ease;
      
      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 8px 20px rgba(0, 123, 255, 0.3);
      }
    }
  }
  
  .schools-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;
    
    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }
    
    @media (max-width: 640px) {
      grid-template-columns: 1fr;
    }
  }
  
  .school-card {
    background: var(--tile-bg, rgba(255, 255, 255, 0.8));
    backdrop-filter: blur(20px);
    border-radius: 20px;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    border: 1px solid var(--tile-border, rgba(0, 0, 0, 0.1));
    
    &:hover {
      transform: translateY(-8px);
      box-shadow: var(--tile-shadow, 0 16px 40px rgba(0, 0, 0, 0.15));
      
      .school-image img {
        transform: scale(1.1);
      }
    }
    
    .school-image {
      position: relative;
      width: 100%;
      height: 180px;
      overflow: hidden;
      background: var(--profile-avatar-bg, #f5f5f7);
      
      .school-video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
        z-index: 1;
      }
      
      .school-img-fallback {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.6s ease;
        display: none;
        
        &.show {
          display: block;
        }
      }
      
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.6s ease;
        display: none;
        
        &.show {
          display: block;
        }
      }
      
      .school-overlay {
        position: absolute;
        top: 12px;
        right: 12px;
        z-index: 2;
        
        .school-badge {
          padding: 6px 12px;
          background: rgba(0, 0, 0, 0.6);
          backdrop-filter: blur(10px);
          color: white;
          font-size: 12px;
          font-weight: 600;
          border-radius: 20px;
        }
      }
      
      .school-mute-toggle {
        position: absolute;
        bottom: 12px;
        left: 12px;
        z-index: 3;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background: rgba(0, 0, 0, 0.6);
        backdrop-filter: blur(10px);
        border: none;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        transition: all 0.3s ease;
        
        svg {
          width: 20px;
          height: 20px;
          fill: currentColor;
        }
        
        &:hover {
          background: rgba(0, 0, 0, 0.8);
          transform: scale(1.1);
        }
        
        &.muted {
          svg {
            opacity: 0.6;
          }
        }
      }
    }
    
    .school-info {
      padding: 20px;
      
      h4 {
        font-size: 18px;
        font-weight: 700;
        color: var(--text-primary, #1d1d1f);
        margin: 0 0 8px 0;
      }
      
      p {
        font-size: 14px;
        color: var(--text-secondary, #6e6e73);
        margin: 0;
        line-height: 1.5;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
    }
    
    // Shimmer 효과
    &.shimmer-card {
      pointer-events: none;
      
      .shimmer-image {
        width: 100%;
        height: 180px;
        background: linear-gradient(90deg, var(--tile-border) 0%, var(--tile-bg) 50%, var(--tile-border) 100%);
        background-size: 200% 100%;
        animation: shimmer 1.5s infinite;
      }
      
      .shimmer-line {
        height: 14px;
        border-radius: 7px;
        background: linear-gradient(90deg, var(--tile-border) 0%, var(--tile-bg) 50%, var(--tile-border) 100%);
        background-size: 200% 100%;
        animation: shimmer 1.5s infinite;
        margin-bottom: 8px;
        
        &.shimmer-title {
          width: 70%;
          height: 18px;
        }
        
        &.shimmer-desc {
          width: 100%;
        }
      }
    }
  }
  
  .empty-state {
    text-align: center;
    padding: 60px 20px;
    
    svg {
      opacity: 0.3;
      margin-bottom: 20px;
    }
    
    h4 {
      font-size: 20px;
      font-weight: 700;
      color: var(--text-primary, #1d1d1f);
      margin: 0 0 8px 0;
    }
    
    p {
      font-size: 14px;
      color: var(--text-secondary, #6e6e73);
      margin: 0;
    }
  }
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

// FAB Speed Dial
.fab-container {
  position: fixed;
  right: 32px;
  bottom: 32px;
  z-index: 1000;
  
  @media (max-width: 768px) {
    right: 20px;
    bottom: 20px;
  }
}

.fab-speed-dial {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 12px;
  
  .fab-actions {
    display: flex;
    flex-direction: column;
    gap: 12px;
    animation: fabSlideIn 0.3s ease;
  }
  
  .fab-action {
    width: 48px;
    height: 48px;
    border-radius: 24px;
    border: none;
    background: var(--tile-background, rgba(255, 255, 255, 0.9));
    backdrop-filter: blur(20px);
    color: var(--accent-color, #007AFF);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease;
    
    svg {
      width: 24px;
      height: 24px;
    }
    
    &:hover {
      transform: scale(1.1);
      background: var(--accent-color, #007AFF);
      color: white;
    }
  }

  .fab-main {
    width: 56px;
    height: 56px;
    border-radius: 28px;
    border: none;
    background: linear-gradient(135deg, #007AFF, #0055FF);
    color: white;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 8px 24px rgba(0, 123, 255, 0.4);
    transition: all 0.3s ease;
    
    svg {
      width: 24px;
      height: 24px;
      transition: transform 0.3s ease;
    }
    
    &:hover {
      transform: scale(1.1);
      box-shadow: 0 12px 32px rgba(0, 123, 255, 0.5);
    }
  }
  
  &.fab-open .fab-main svg {
    transform: rotate(45deg);
  }
}

@keyframes fabSlideIn {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.8);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

